import {useState, useEffect} from "react";
import UserNav from "./UserNav";

const Navbar = ({ isMobile, useOutsideClick }) => {
	const [isUserNavOpen, setIsUserNavOpen] = useState(false)
	const [isFeaturesListOpen, setIsFeaturesListOpen] = useState(false)
	const [isCompanyListOpen, setIsCompanyListOpen] = useState(false)



	const openUserNav = () => {
		isUserNavOpen ? setIsUserNavOpen(false) : setIsUserNavOpen(true)
	}

	const closeUserNav = (e) => {
		if (e.target !== e.currentTarget) return
		setIsUserNavOpen(false)
		setIsFeaturesListOpen(false)
		setIsCompanyListOpen(false)
	}

	const closeFeaturesList = (e) => {
		setIsFeaturesListOpen(false)
	}

	const closeCompanyList = (e) => {
		setIsCompanyListOpen(false)
	}


	const toggleFeaturesList = (e) => {
		isFeaturesListOpen ? setIsFeaturesListOpen(false) : setIsFeaturesListOpen(true)
	}
	const toggleCompanyList = () => {
		isCompanyListOpen ? setIsCompanyListOpen(false) : setIsCompanyListOpen(true)
	}

	const featuresRef = useOutsideClick(closeFeaturesList);
	const companyRef = useOutsideClick(closeCompanyList);

// Preload images
	useEffect(() => {
		const images = [
		"./img/icon-todo.svg",
		"./img/icon-calendar.svg",
		"./img/icon-reminders.svg",
		"./img/icon-planning.svg",
		];
	
		images.forEach((image) => {
		const img = new Image();
		img.src = image;
		});
	}, []);

	return (
		<nav className="w-full bg-almostWhite">
			{isMobile ? 
			(
				<div className="p-4 grid grid-cols-2">
					<div>
						<img src="./img/logo.svg"></img>
					</div>
					<div className="flex justify-end items-center">
						<img src="./img/icon-menu.svg"
						className="cursor-pointer"
						onClick={openUserNav}
						></img>
					</div>
					{isUserNavOpen &&
					<div
					className="fixed top-0 left-0 w-full h-full bg-black bg-opacity-20 flex justify-end"
					onClick={(e) => closeUserNav(e)}
					>
						<div className="p-2 md:p-6 ml-16 flex flex-col tablet:flex-row gap-4 tablet:gap-8 h-full w-3/5 sm:w-1/3 bg-white overflow-y-auto">
							<div className="flex justify-end cursor-pointer">
								<img src="./img/icon-close-menu.svg" onClick={closeUserNav}></img>
							</div>
							<div className="flex items-center cursor-pointer" onClick={toggleFeaturesList}>
								<span>Features</span>
								<img src={isFeaturesListOpen ? "./img/icon-arrow-up.svg" : "./img/icon-arrow-down.svg"} className="ml-2"></img>
							</div>
							{isFeaturesListOpen &&
								<ul className="md:ml-4">
									<li className="flex items-center flex-row gap-4 cursor-pointer p-4"><img src="./img/icon-todo.svg"></img><span>Todo List</span></li>
									<li className="flex items-center flex-row gap-4 cursor-pointer p-4"><img src="./img/icon-calendar.svg"></img><span>Calendar</span></li>
									<li className="flex items-center flex-row gap-4 cursor-pointer p-4"><img src="./img/icon-reminders.svg"></img><span>Reminders</span></li>
									<li className="flex items-center flex-row gap-4 cursor-pointer p-4 mb-4"><img src="./img/icon-planning.svg"></img><span>Planning</span></li>
								</ul>
							}
							<div className="flex items-center cursor-pointer" onClick={toggleCompanyList}>
								<span>Company</span>
								<img src={isCompanyListOpen ? "./img/icon-arrow-up.svg" : "./img/icon-arrow-down.svg"} className="ml-2"></img>
							</div>
							{isCompanyListOpen && 
								<ul className="md:ml-4">
									<li className="flex items-center flex-row gap-4 cursor-pointer p-4">History</li>
									<li className="flex items-center flex-row gap-4 cursor-pointer p-4">Our Team</li>
									<li className="flex items-center flex-row gap-4 cursor-pointer p-4 mb-4">Blog</li>
								</ul>
							}
							<div className="flex items-center cursor-pointer">
								<span>Careers</span>
							</div>
							<div className="flex items-center cursor-pointer">
								<span>About</span>
							</div>
							<div className="w-full">
								<UserNav isMobile={isMobile}></UserNav>
							</div>
						</div>
					</div>}
				</div>
			)
		:
		(<div className="grid grid-cols-2">
		<div className="flex items-center">
			<div className="cursor-pointer">
				<img src="./img/logo.svg"></img>
			</div>
			<div className="relative p-6 ml-16 flex flex-col md:flex-row gap-4 md:gap-8 h-full w-3/5 bg-gray-50">
				<div className="relative">
					<div className="flex items-center cursor-pointer p-4 md:p-0 hover:text-black hover:font-bold"
					onClick={toggleFeaturesList}
					ref={featuresRef}
					>
						<span>Features</span>
						<img src={isFeaturesListOpen ? "./img/icon-arrow-up.svg" : "./img/icon-arrow-down.svg"} className="ml-2"></img>
					</div>
						{isFeaturesListOpen &&
						<div className="md:absolute left-0">
							<ul className="w-full whitespace-nowrap text-md bg-white shadow-2xl rounded-lg">
								<li className="flex items-center flex-row gap-4 cursor-pointer py-4 justify-center w-full hover:bg-gray-400 rounded-t-lg">
									<img src="./img/icon-todo.svg" className="-translate-y-0.5">
									</img>
									<span>Todo List</span>
								</li>
								<li className="flex items-center flex-row gap-4 cursor-pointer p-4 justify-center w-full hover:bg-gray-400">
									<img src="./img/icon-calendar.svg" className="-translate-y-0.5"></img>
									<span>Calendar</span>
								</li>
								<li className="flex items-center flex-row gap-4 cursor-pointer p-4 justify-center w-full hover:bg-gray-400">
									<img src="./img/icon-reminders.svg" className="-translate-y-0.5"></img>
									<span>Reminders</span>
								</li>
								<li className="flex items-center flex-row gap-4 cursor-pointer p-4 mb-4 md:mb-0 hover:bg-gray-400 rounded-b-lg">
									<img src="./img/icon-planning.svg" className="-translate-y-0.5"></img>
									<span>Planning</span>
								</li>
							</ul>
						</div>
						}
				</div>
				<div className="relative">
					<div className="flex items-center cursor-pointer md:pd-0 hover:text-black hover:font-bold" onClick={toggleCompanyList} ref={companyRef}>
						<span>Company</span>
						<img src={isCompanyListOpen ? "./img/icon-arrow-up.svg" : "./img/icon-arrow-down.svg"} className="ml-2"></img>
					</div>
					{isCompanyListOpen && 
					<div className="absolute left-0">
						<ul className="w-full whitespace-nowrap text-md bg-white shadow-2xl rounded-lg">
							<li className="flex items-center flex-row gap-4 cursor-pointer p-4 justify-center w-full hover:bg-gray-400 rounded-t-lg">History</li>
							<li className="flex items-center flex-row gap-4 cursor-pointer p-4 justify-center w-full hover:bg-gray-400">Our Team</li>
							<li className="flex items-center flex-row gap-4 cursor-pointer p-4 justify-center w-full hover:bg-gray-400 rounded-b-lg">Blog</li>
						</ul>
					</div>
					}	
				</div>
				<div className="flex items-center cursor-pointer hover:text-black hover:font-bold">
					<span>Careers</span>
				</div>
				<div className="flex items-center cursor-pointer hover:text-black hover:font-bold">
					<span>About</span>
				</div>
			</div>
		</div>
		<div className="flex items-center justify-end">
			<UserNav></UserNav>
		</div>
	</div>)
		}
			
		</nav>
	)
}

export default Navbar