import React from "react"
import { Button } from "@material-tailwind/react";

const Main = () => {

    return (
        <div className="flex flex-col-reverse md:grid md:grid-cols-2 2xl:pt-12 w-full lg:relative bg-almostWhite">
            <div className="flex justify-center items-center">
                <div className="p-2 xl:px-16 2xl:pt-12 2xl:px-36">
                    <h1 className="text-4xl md:text-6xl lg:text-6xl xl:text-8xl 2xl:text-9xl font-bold text-center md:text-left">Make remote work</h1>
                    <p className="pt-12 text-md md:text-xl 2xl:text-3xl text-mediumGray text-center md:text-left">
                        Get your team in sync, no matter your location.
                        Streamline processes, create team rituals and watch productivity soar.</p>
                    <div className="flex justify-center pt-6 md:pt-12 2xl:pt-24 md:justify-start">
                        <Button
                        className="bg-almostBlack
                        shadow-almostBlack
                        hover:shadow-almostBlack 
                        normal-case
                        font-Epilogue
                        tracking-wide
                        text-sm
                        2xl:text-xl
                        ">Learn more</Button>
                    </div>
                    <div className="pt-12 md:pt-24 w-full 2xl:pt-36">
                        <div className="grid grid-cols-4 md:flex md:gap-4 2xl:gap-16 px-4">
                            <div className="flex justify-center items-center">
                                <img src="./img/client-databiz.svg"></img>
                            </div>
                            <div className="flex justify-center items-center">
                                <img src="./img/client-audiophile.svg"></img>
                            </div>
                            <div className="flex justify-center items-center">
                                <img src="./img/client-meet.svg"></img>
                            </div>
                            <div className="flex justify-center items-center">
                                <img src="./img/client-maker.svg"></img>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <picture className="max-w-full ml-auto mr-auto flex items-center xl:w-4/5 2xl:w-3/5">
                <source media="(min-width: 720px)" srcSet="./img/image-hero-desktop.png"></source>
                <img src="./img/image-hero-mobile.png" alt="Hero Image"></img>
            </picture>
        </div>
    )
}

export default Main