import { useEffect, useState, useRef } from 'react';
import Main from './components/Main';
import Navbar from './components/Navbar';

function App() {
  const [isMobile, setIsMobile] = useState(window.innerWidth < 820)

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 820);
    };

    // Set initial value on component mount
    handleResize();

    // Add event listener to update the state when window is resized
    window.addEventListener('resize', handleResize);

    // Clean up the event listener on component unmount
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const useOutsideClick = (callback) => {
		const ref = useRef();
    
		useEffect(() => {
		const handleClick = (event) => {
			if (ref.current && !ref.current.contains(event.target)) {
			  callback();
			}
		  };
	  
		  document.addEventListener('click', handleClick, true);
	  
		  return () => {
			document.removeEventListener('click', handleClick, true);
		  };
		}, [ref]);
	  
		return ref;
	  };

  return (
    <div className="w-screen h-screen md:p-8 font-Epilogue text-md bg-almostWhite text-almostBlack">
      <div className='sticky top-0 z-10'>
        <Navbar isMobile={isMobile} useOutsideClick={useOutsideClick}></Navbar>
      </div>
      <div className='z-0'>
        <Main></Main>
      </div>
    </div>
  );
}

export default App;
